.add-btn {
  flex: 1 1 100%;
  height: 38px;
  color: #bdbdbd;
  background: #0b0b0b;
  padding: 0;
  margin: 0;
  border: 1px solid #328588;
  cursor: pointer;
  transition: color 200ms cubic-bezier(0, 0, 0.2, 1) 0ms,
    border-color 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
}

.add-btn:hover {
  color: #fff;
  border-color: #5df8ff;
}

.add-btn-wrapper {
  margin: 10px 0 0 0;
  width: 100%;
  display: flex;
}
